___CSS_LOADER_EXPORT___.push([module.id, `body {
  background-color: whitesmoke;
  margin: 1px;
}
#container {
  display: grid;
  grid-template-columns: 400px 200px;
  grid-template-rows: auto 1fr;
  gap: 2px;
  height: 99.8vh;
}
#header {
  grid-column: 1/4;
  grid-row: 1/2;
  text-align: center;
  border: 1px solid black;
}
#sidebar-container {
  border: 1px solid;
  grid-column: 1/2;
  display: flex;
  flex-direction: column;
  align-items: start;
  padding: 20px;
}

#task-container {
  border: 1px solid;
  grid-column: 2/4;
  text-align: center;
}
button {
  font-size: 1.2em;
  cursor: pointer;
}
